import React, {useEffect, useState } from 'react';
import { Nav } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import '../App.css';
import data from '../data.js';
import classNames from 'classnames';
import Slider from 'react-slick';
function DanceNFilm(isMobile) {
  let [imgTable] = useState(data[2]);
  let [phraseTable] = useState(data[3]);

  var settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 3,
    slidesToScroll: 1
  };
  
  return (
    <>
      <main className={classNames('main-ctnr', {'mobile-top-space' : isMobile, 'mt-nw-1' : !isMobile  })}>
        <div className={classNames('ps-relative')}>
          <div className={classNames('banner-dv p-nw-1')}>
            <h3 className='items-baseline'>ON PROJECTS</h3>
            <Slider {...settings}>
            {
              imgTable.filter((imgData) => imgData.groupCd == 'SLIG').map((imgData,i) => { 
                return (
                  <Banner imgTable={imgTable[i]} key={i}></Banner>
                )
              })
            }
            </Slider>
            <div className='mtb-nw-1'>
              {/* <a className='sp-button mr-nw-1 txt-no-underline' href="#">Join now</a>
              <a className='sp-button txt-no-underline' href="#">learn more</a> */}
            </div>
          </div>
          <div className='video-space'>
            <video autoPlay loop muted className='video-inner-video'><source src='/video/meow.mp4' type='video/mp4' /></video>
            <div className='video-inner-up-bg dp-flex ps-absolute'>
            </div>
            <div className='lt-ul-cntr dp-flex ps-absolute dp-flex-space-around'>
              {/* <ul className="lt-ul no-ul-deco">
                  <li className='sns-list'><img className='sns-img' src={youtube_link}></img>@random</li>
                  <li className='sns-list'><img className='sns-img' src={instar_link}></img>@random</li>
                  <li className='sns-list'><img className='sns-img' src={kakao_link}></img>@random</li>
              </ul> */}
              <h2 className='slogan ft-color'>⭐Just 'DO' whatever you want</h2>
            </div>
            <div className='video-inner-dw-bg dp-flex ps-absolute'>
            </div>
          </div>
          <div className="stream-cards-dv s-cs-bg">
            <section className='cards-section'>
              {
                phraseTable.filter((prseData) => prseData.groupCd == 'CADS').map((prseData,i) => {
                  return (
                    <Card prseData={prseData} imgTable={imgTable} key={i} i={i}></Card>
                  )
                })
              }
            </section>
          </div>
          <div className="global-gutters invisible ps-fixed">
            <button className='invisible'><a className='start-an-order visible layerButtonPd' href="#">문의 남기기</a></button>
          </div>
        </div>
      </main>
    </>
  )
}

function Banner(props) {
  return (
    <div className='dp-flex dp-flex-space-around'>
      {/* <div className='img-frame'>
        <img className='m-auto card-img' src={props.imgTable.imgSrc}></img>
      </div> */}
      <section>
        <span className='cmm-ft-size ft-size-1'>
          {props.imgTable.imgDesc3}<br></br>
          {props.imgTable.date}
        </span>
        <p className='cmm-ft-size mtb-nw-3 ft-size-2'>{props.imgTable.imgTitle}<br></br>{props.imgTable.imgDesc1}</p>
        <span className='cmm-ft-size ft-size-1'>
          {props.imgTable.imgDesc2}
        </span>
      </section>
    </div>
  )
}

function Card(props) {
  return (
    <article className='sp-card'>
        {
          props.imgTable.filter((imgData) => imgData.imgId == props.prseData.imgId).map(imgData => {
            return (
              <>
                <div className='artist-hover ps-relative'>
                  <div className='artist-box txt-center'>
                    <img className='artist-img' src={imgData.imgSrc} key={props.i}></img>
                  </div>
                  <h4 className='artist-dtl'>{props.prseData.phrse1Desc}</h4>
                </div>
              </>
            )
          })
        }
    </article>
  )
}

export default DanceNFilm;